@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
* {
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: white;
}
ul{
  list-style-type: none;
}
body {
  background-color: #2a2a72;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  margin: 0;
}

.user-form {
  width: 100%;
  max-width: 700px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.user-form input {
  width: 100%;
  display: block;
  background-color: #4c2885;
  border: none;
  border-radius: 10px;
  color: #fff;
  padding: 1rem;
  margin-bottom: 2rem;
  font-family: inherit;
  font-size: 1rem;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05),
    0 15px 40px rgba(0, 0, 0, 0.1);
}

.user-form input::-webkit-input-placeholder {
  color: #bbb;
}

.user-form input:-ms-input-placeholder {
  color: #bbb;
}

.user-form input::placeholder {
  color: #bbb;
}

.user-form input:focus {
  outline: none;
}

.card {
  max-width: 800px;
  background-color: #4c2885;
  border-radius: 20px;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05),
    0 15px 40px rgba(0, 0, 0, 0.1);
  display: -webkit-flex;
  display: flex;
  padding: 3rem;
  margin: 0 1.5rem;
}

.avatar {
  border-radius: 50%;
  border: 10px solid #2a2a72;
  height: 150px;
  width: 150px;
}

.user-info {
  color: #eee;
  margin-left: 2rem;
}
 p{
   max-width: 300px;
 }
.user-info h2 {
  margin-top: 0;
}

.user-info ul {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0;
  max-width: 400px;
}

.user-info ul li {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.user-info ul li strong {
  font-size: 0.9rem;
  margin-left: 0.5rem;
}
button{
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  background-color: #ffffff;
  color:#4c2885;
  margin-bottom: 2rem;
  cursor: pointer;
  font-weight: bold;
  font-size: medium;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05),
  0 15px 40px rgba(0, 0, 0, 0.1);
}
button:active{
  -webkit-transform: scale(0.90);
          transform: scale(0.90);
}
@media (max-width: 500px) {
  .card {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  .user-form {
    max-width: 400px;
  }
}

